.top-line {
	background-color: $dark;
	position: relative;
	height: 65px;
}
.logo {
font-weight: 800;
	color:#fff;
	font-size: 23px;
	display: inline-block;
	text-decoration: none;
	position: absolute;
	padding-left: 30px;
	left:0;
	line-height: 65px;
}
.logo:hover,
.logo:focus, {
	text-decoration: none;
	color:#fff;
}
.logo-litera span {
	color:$accent;
// text-decoration: underline;
}

.main-menu {
	padding: 0 100px;
	width: 100%;
	text-align: center;
}

.main-menu ul,
.main-menu li {
	list-style-type: none;
	padding: 0;
	margin: 0;
}
.main-menu li {
	display: inline-block;
}
.main-menu li.active {
	color:#fff;
	background-color: lighten($dark, 5);

}
.main-menu li a {
	color:#d1d9e0;
	text-decoration: none;
	text-transform: uppercase;
	font-weight: bold;
	padding: 0 15px;
	line-height: 65px;
	display: block;
	transition: color 0.25s ease, background-color 0.25s ease;
}

.main-menu li a:hover {
	color:#fff;
	background-color: lighten($dark, 8);
}

.search-wrap .search{
	position: absolute;
	right: 0;
	padding: 0 30px 0 10px;
	line-height: 65px;
	color:#5c6470;
	cursor: pointer;
	text-align: center;
	transition: color 0.25s ease;
}

.search-wrap .search:hover{
	color: lighten(#5c6470, 10);
}

.search-wrap .search-field {
	display: none;
	position: absolute;
	right:0;
	top:65px;
	padding: 15px;
	background-color: lighten($dark, 3%);
	width: 100%;
	border-top:lighten($dark, 59%) 1px solid;
	z-index: 4;
}

.search-wrap .search-field input[type=text] {
	width: 100%;
	padding: 8px;
	border:lighten($dark, 10%) 1px solid;
	background-color: lighten($dark, 8%);
	color:#fff;
	outline: none;
}
.search-wrap .search-field input[type=text]:active,
.search-wrap .search-field input[type=text]:hover,
.search-wrap .search-field input[type=text]:focus {
	border-color: lighten($dark, 15%);
}

.mobile-menu-button {
	display: none;
	color:#fff;
	cursor: pointer;
	line-height: 65px;
	font-size: 17px;
	font-weight: bold;
	text-transform: uppercase;
	white-space:nowrap;
}

.mobile-menu {
	display: none;
	background-color: 	lighten($dark, 3%);
	border-top: lighten($dark, 3%) 1px solid;
}

.mobile-menu .top-menu{
	display: none;
}

.mobile-menu .top-menu li {
	display: block;
}

.ad {
	text-align: center;
	width: 100%;
	display: block;
	overflow: hidden;
}
.top-ad {
	margin-bottom: 20px;
