section {
	background-color: #fafafa;
	padding: 20px 0;
}
section h2 {
	text-transform: uppercase;
	margin:0;
	font-size: 23px;
	font-weight: 600;
}
.col-md-6 {
	width: (100 / 2) * 1%;
	float:left;
}
.col-md-4 {
	width: (100 / 3) * 1%;
	float:left;
}
.col-md-4:last-child {
	float:right;
}

.pattern .item-title {
	background-size: cover;
	background-position: center center;
	min-height: 340px;
	position: relative;
}
.pattern .item-title .item-title-content {
	position: absolute;
	bottom: 0;
	color:#fff;
	padding: 40px;
	z-index: 41;

}
.pattern .item-title .item-title-content h3 {
	margin: 0;
	text-transform: uppercase;
	font-weight: 600;
	line-height: 1.25;
	font-size: 19px;
}
.pattern .item-title .item-title-content h3 a {
	color:#fff;
	text-decoration: none;
	transition: all 0.25s ease;
}

.pattern .item-title .item-title-content h3 a:hover {
	text-decoration: underline;
}


.pattern {
	background-color: $dark;
	position: relative;
	display: block;
}
.pattern:before {
	content:'';
	opacity: 0;
	transition:opacity .25s ease,
	top .25s ease,
	left .25s ease,
	bottom .25s ease,
	right .25s ease;
	z-index: 1;
	position: absolute;
	border:1px solid #fff;
	top:0;
	right:0;
	left:0;
	bottom:0;

}
.pattern:hover:before {
	top:10px;
	right:10px;
	left:10px;
	bottom:10px;
	opacity: 0.2;
}
.pattern .overlay  {
	background-position: center center;
	background-size: cover;
	position: relative;
	display: block;
}
.pattern .overlay:before  {
	content:'';
	opacity: 0.55;
	background-color:$dark;
	transition:opacity .25s ease;
	position: absolute;
	top:0;
	right:0;
	left:0;
	bottom:0;
}
.pattern:hover .overlay::before,
.pattern .overlay:hover::before  {
	opacity: 0.75;
}







