html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: Arial, "Helvetica CY", "Nimbus Sans L", sans-serif;
  overflow-x: hidden;
  color: #000000;
  font-display: auto;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.top-line {
  background-color: #202222;
  position: relative;
  height: 65px;
}

.logo {
  font-weight: 800;
  color: #fff;
  font-size: 23px;
  display: inline-block;
  text-decoration: none;
  position: absolute;
  padding-left: 30px;
  left: 0;
  line-height: 65px;
}

.logo:hover,
.logo:focus {
  text-decoration: none;
  color: #fff;
}

.logo-litera span {
  color: #ec1c1c;
}

.main-menu {
  padding: 0 100px;
  width: 100%;
  text-align: center;
}

.main-menu ul,
.main-menu li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.main-menu li {
  display: inline-block;
}

.main-menu li.active {
  color: #fff;
  background-color: #2c2f2f;
}

.main-menu li a {
  color: #d1d9e0;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  padding: 0 15px;
  line-height: 65px;
  display: block;
  transition: color 0.25s ease, background-color 0.25s ease;
}

.main-menu li a:hover {
  color: #fff;
  background-color: #343737;
}

.search-wrap .search {
  position: absolute;
  right: 0;
  padding: 0 30px 0 10px;
  line-height: 65px;
  color: #5c6470;
  cursor: pointer;
  text-align: center;
  transition: color 0.25s ease;
}

.search-wrap .search:hover {
  color: #737d8c;
}

.search-wrap .search-field {
  display: none;
  position: absolute;
  right: 0;
  top: 65px;
  padding: 15px;
  background-color: #272a2a;
  width: 100%;
  border-top: #b5baba 1px solid;
  z-index: 4;
}

.search-wrap .search-field input[type=text] {
  width: 100%;
  padding: 8px;
  border: #393c3c 1px solid;
  background-color: #343737;
  color: #fff;
  outline: none;
}

.search-wrap .search-field input[type=text]:active,
.search-wrap .search-field input[type=text]:hover,
.search-wrap .search-field input[type=text]:focus {
  border-color: #454949;
}

.mobile-menu-button {
  display: none;
  color: #fff;
  cursor: pointer;
  line-height: 65px;
  font-size: 17px;
  font-weight: bold;
  text-transform: uppercase;
  white-space: nowrap;
}

.mobile-menu {
  display: none;
  background-color: #272a2a;
  border-top: #272a2a 1px solid;
}

.mobile-menu .top-menu {
  display: none;
}

.mobile-menu .top-menu li {
  display: block;
}

.ad {
  text-align: center;
  width: 100%;
  display: block;
  overflow: hidden;
}

.top-ad {
  margin-bottom: 20px;
}

.container-fluid {
  max-width: 1346px;
  padding: 0 20px;
}

section {
  background-color: #fafafa;
  padding: 20px 0;
}

section h2 {
  text-transform: uppercase;
  margin: 0;
  font-size: 23px;
  font-weight: 600;
}

.col-md-6 {
  width: 50%;
  float: left;
}

.col-md-4 {
  width: 33.33333%;
  float: left;
}

.col-md-4:last-child {
  float: right;
}

.pattern .item-title {
  background-size: cover;
  background-position: center center;
  min-height: 340px;
  position: relative;
}

.pattern .item-title .item-title-content {
  position: absolute;
  bottom: 0;
  color: #fff;
  padding: 40px;
  z-index: 41;
}

.pattern .item-title .item-title-content h3 {
  margin: 0;
  text-transform: uppercase;
  font-weight: 600;
  line-height: 1.25;
  font-size: 19px;
}

.pattern .item-title .item-title-content h3 a {
  color: #fff;
  text-decoration: none;
  transition: all 0.25s ease;
}

.pattern .item-title .item-title-content h3 a:hover {
  text-decoration: underline;
}

.pattern {
  background-color: #202222;
  position: relative;
  display: block;
}

.pattern:before {
  content: '';
  opacity: 0;
  transition: opacity .25s ease, top .25s ease, left .25s ease, bottom .25s ease, right .25s ease;
  z-index: 1;
  position: absolute;
  border: 1px solid #fff;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.pattern:hover:before {
  top: 10px;
  right: 10px;
  left: 10px;
  bottom: 10px;
  opacity: 0.2;
}

.pattern .overlay {
  background-position: center center;
  background-size: cover;
  position: relative;
  display: block;
}

.pattern .overlay:before {
  content: '';
  opacity: 0.55;
  background-color: #202222;
  transition: opacity .25s ease;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.pattern:hover .overlay::before,
.pattern .overlay:hover::before {
  opacity: 0.75;
}

.item-social {
  margin-top: 10px;
}

.item-social .fa {
  font-size: 12px;
  margin-right: 5px;
}

.item-comments {
  display: inline-block;
  margin-right: 20px;
}

.item-comments .item-comments-count {
  font-size: 12px;
  color: #fff;
  position: relative;
  display: inline-block;
  text-decoration: none;
  transition: opacity .25s ease;
}

.item-comments .item-comments-count:hover {
