}

.likely-custom {
  display: inline-block;
  font-size: 12px;
}

.likely-custom > div {
  padding: 0 4px 0 3px;
  position: relative;
  cursor: pointer;
  margin: 6px 2px 0;
  border: 1px solid #fff;
  border-radius: 2px;
